import PropTypes from 'prop-types'
import useActiveWeb3React from "../../hooks/useWeb3";
import {useCurrencyBalance} from "../../state/wallet/hooks";

ListToken.propTypes = {
    currency: PropTypes.any,
    item: PropTypes.any,
    selectedToken: PropTypes.any,
    tokenSelected: PropTypes.func
}
export default function ListToken ({currency, item, selectedToken, tokenSelected}) {
    const {account} = useActiveWeb3React()
    const currencyBalance = useCurrencyBalance(account ?? undefined, currency ?? undefined)

    return (
        <a
            href="javascript:void(0);"
            key={item?.address}
            onClick={() => {tokenSelected(item)}}
            disabled={selectedToken?.address?.toLowerCase() === item?.address?.toLowerCase()}
            className="tokens-list-item p-3 border-b last:border-b-0 border-gray-100 border-opacity-10 flex flex-row items-center justify-between hover:bg-black hover:bg-opacity-5 dark:hover:bg-white dark:hover:bg-opacity-25"
        >
            <div className="token-info flex flex-row items-center justify-start">
                <div className="tokan-icon mr-2">
                    <img src={`${item?.tokenImg}?w=248&fit=crop&auto=format`} width={32} alt="" />
                </div>
                <div className="tokan-info">
                    <div className="text-black dark:text-white text-sm font-semibold mb-1">
                        {
                            item?.name
                        }
                    </div>
                    <div className="text-black dark:text-white text-opacity-50 text-xs">
                        {
                            item?.symbol
                        }
                    </div>
                </div>
            </div>
            <div className="flex flex-row items-center justify-end">
                <div className="text-black dark:text-white text-opacity-50 text-2sm mr-2">
                    {`${currencyBalance?.toSignificant(6) ?? 'Loading'}`}
                </div>
            </div>
        </a>
        // <ListItem
        //     key={item?.address} button
        //     selected={(selectedToken?.address?.toLowerCase() === item?.address?.toLowerCase()) || (selectedToken?.symbol?.toLowerCase() === item?.symbol?.toLowerCase())}
        //     onClick={() => {tokenSelected(item)}}
        //     disabled={selectedToken?.address?.toLowerCase() === item?.address?.toLowerCase()}
        //     secondaryAction={
        //         <Typography
        //         >
        //             {`${currencyBalance?.toSignificant(6) ?? 'Loading'}`}
        //         </Typography>
        //     }
        // >
        //     <ListItemAvatar>
        //         <Avatar
        //             src={`${item?.tokenImg}?w=248&fit=crop&auto=format`}
        //             srcSet={`${item?.tokenImg}?w=248&fit=crop&auto=format&dpr=2 2x`}
        //             alt={item?.name}
        //             loading="lazy"
        //         />
        //     </ListItemAvatar>
        //     <ListItemText
        //         primary={item?.name}
        //         secondary={item?.symbol}
        //     />
        // </ListItem>
    )
}