import PropTypes from 'prop-types';
import {Button, Card, CardContent, CardHeader, Grid} from "@material-ui/core";
import React, {useState} from "react";
import {JSBI, Percent} from "@udegoswap/sdk";
import Dots from "../../../components/Loader/Dots";
import {unwrappedToken} from "../../../utils/wrappedCurrency";
import {useTokenBalance} from "../../../state/wallet/hooks";
import useTotalSupply from "../../../hooks/useTotalSupply";
import useActiveWeb3React from "../../../hooks/useWeb3";
import {ApprovalState, useApproveCallbackForRemove} from "../../../hooks/useApproveCallback";
import {UDE_ROUTER} from "../../../config/config";
import {getContract, getDeadline, getMin, showError, showPromise} from "../../../config/helpers";
import tokenAbi, {pairABI, routerABI} from "../../../config/abis";

PoolDetails.propTypes = {
    pair: PropTypes.any
}

export function PoolDetails({pair}) {
    const {account, library} = useActiveWeb3React()

    const currency0 = unwrappedToken(pair.token0)
    const currency1 = unwrappedToken(pair.token1)

    const [showMore, setShowMore] = useState(false)

    const userPoolBalance = useTokenBalance(account ?? undefined, pair.liquidityToken)
    const totalPoolTokens = useTotalSupply(pair.liquidityToken)

    const [isApproveRemove, approvePairCallback] = useApproveCallbackForRemove(pair, UDE_ROUTER)

    const removeLiquid = async () => {
        try{
            const router = await getContract(UDE_ROUTER, routerABI, library.getSigner())
            const pairC = await getContract(pair.liquidityToken.address, pairABI, library.getSigner())
            if(router != null) {
                const totalSupp = await pairC.totalSupply()

                const getTokenAAddr = await pairC.token0()
                const getTokenBAddr = await pairC.token1()

                const tempTokenAContract = await getContract(getTokenAAddr, tokenAbi, library.getSigner())
                const tempTokenBContract = await getContract(getTokenBAddr, tokenAbi, library.getSigner())

                const total0Supp = await tempTokenAContract.balanceOf(pairC.address)
                const total1Supp = await tempTokenBContract.balanceOf(pairC.address)

                const lpTokens = await pairC.balanceOf(account)
                const token0Amount = (lpTokens * total0Supp) / totalSupp
                const token1Amount = (lpTokens * total1Supp) / totalSupp

                const finPoolA = token0Amount
                const finPoolB = token1Amount

                let poolShare = (lpTokens/totalSupp)
                if(poolShare > 99.99) {
                    poolShare = 99.99
                }
                else if(poolShare<0.01){
                    poolShare = 0.01
                }

                // console.log(lpTokens)

                const txn = await router.removeLiquidity(
                    getTokenAAddr,
                    getTokenBAddr,
                    lpTokens,
                    getMin(finPoolA),
                    getMin(finPoolB),
                    account,
                    getDeadline()
                )
                showPromise(txn.wait())
            }
        }catch (e) {
            console.log(e)
            showError(
                e.data ? e.data.message : e.message
            )
        }
    }

    const poolTokenPercentage2 =
        !!userPoolBalance && !!totalPoolTokens && JSBI.greaterThanOrEqual(totalPoolTokens.raw, userPoolBalance.raw)
            ? new Percent(userPoolBalance.raw, totalPoolTokens.raw)
            : undefined

    const [token0Deposited, token1Deposited] =
        !!pair &&
        !!totalPoolTokens &&
        !!userPoolBalance &&
        // this condition is a short-circuit in the case where useTokenBalance updates sooner than useTotalSupply
        JSBI.greaterThanOrEqual(totalPoolTokens.raw, userPoolBalance.raw)
            ? [
                pair.getLiquidityValue(pair.token0, totalPoolTokens, userPoolBalance, false),
                pair.getLiquidityValue(pair.token1, totalPoolTokens, userPoolBalance, false),
            ]
            : [undefined, undefined]
    return (
        parseFloat(userPoolBalance.toSignificant(18)) > 0 &&
        <Grid item xs={12} md={12} sx={{textAlign: 'center'}}>
            <Card>
                <CardHeader
                    title="Pool Details"
                    subheader="Pool balances in your wallet"
                />
                <CardContent>
                    <Grid container spacing={1}>
                        <Grid item xs={6} md={6}>
                            {!currency0 || !currency1 ? <Dots>Loading</Dots> : `${currency0.symbol}/${currency1.symbol} LPs`}
                        </Grid>
                        <Grid item xs={6} md={6}>
                            <b>{userPoolBalance?.toSignificant(4)}</b>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1}>
                        <Grid item xs={6} md={6}>
                            {currency0.symbol} Tokens
                        </Grid>
                        <Grid item xs={6} md={6}>
                            <b>
                                {token0Deposited ? (
                                    token0Deposited?.toSignificant(6)
                                ) : (
                                    '-'
                                )}
                            </b>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1}>
                        <Grid item xs={6} md={6}>
                            {currency1.symbol} Tokens
                        </Grid>
                        <Grid item xs={6} md={6}>
                            <b>
                                {token1Deposited ? (
                                    token1Deposited?.toSignificant(6)
                                ) : (
                                    '-'
                                )}
                            </b>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1}>
                        <Grid item xs={6} md={6}>
                            Pool Shares
                        </Grid>
                        <Grid item xs={6} md={6}>
                            <b style={{color: 'green'}}>
                                {poolTokenPercentage2
                                    ? `${poolTokenPercentage2.toFixed(2) === '0.00' ? '<0.01' : poolTokenPercentage2.toFixed(2)}%`
                                    : '-'}
                            </b>
                        </Grid>
                    </Grid>
                    <Grid container spacing={1}>
                        <Grid item xs={6} md={6}>
                            <Button variant="contained" fullWidth sx={{maxWidth: 620, borderRadius: 25}} onClick={approvePairCallback} size="large" disabled={isApproveRemove === ApprovalState.APPROVED}>
                                Approve to Remove
                            </Button>
                        </Grid>
                        <Grid item xs={6} md={6}>
                            <Button
                                variant="contained"
                                color="error"
                                onClick={removeLiquid}
                                fullWidth
                                sx={{maxWidth: 620, borderRadius: 25}}
                                size="large"
                                disabled={isApproveRemove !== ApprovalState.APPROVED || parseFloat(userPoolBalance.toFixed(2)) <= 0}>
                                Remove Liquidity
                            </Button>
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </Grid>
    );
}