import {CurrencyAmount, ETHER, JSBI, Percent, Price, Token} from '@udegoswap/sdk';
import { useCallback, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useActiveWeb3React from '../../hooks/useWeb3';
import { PairState, useUDEPair } from '../../hooks/useUDEPairs';
import useTotalSupply from '../../hooks/useTotalSupply';

import { wrappedCurrency, wrappedCurrencyAmount } from '../../utils/wrappedCurrency';
import { tryParseAmount } from '../swap/hooks';
import { useCurrencyBalances } from '../wallet/hooks';
import { Field, typeInput } from './actions';
import {lessThan} from "../../utils";

const ZERO = JSBI.BigInt(0);
export function useMintState() {
    return useSelector((state) => state.mint);
}
export function useMintActionHandlers(noLiquidity) {
    const dispatch = useDispatch();
    const onFieldAInput = useCallback((typedValue) => {
        dispatch(typeInput({ field: Field.CURRENCY_A, typedValue, noLiquidity: noLiquidity === true }));
    }, [dispatch, noLiquidity]);
    const onFieldBInput = useCallback((typedValue) => {
        dispatch(typeInput({ field: Field.CURRENCY_B, typedValue, noLiquidity: noLiquidity === true }));
    }, [dispatch, noLiquidity]);
    return {
        onFieldAInput,
        onFieldBInput,
    };
}
export function useDerivedMintInfo(currencyA, currencyB) {
    const { account, chainId } = useActiveWeb3React();
    const { independentField, typedValue, otherTypedValue } = useMintState();
    const dependentField = independentField === Field.CURRENCY_A ? Field.CURRENCY_B : Field.CURRENCY_A;
    // tokens
    const currencies = useMemo(() => ({
        [Field.CURRENCY_A]: currencyA !== null && currencyA !== undefined ? currencyA : undefined,
        [Field.CURRENCY_B]: currencyB !== null && currencyB !== undefined ? currencyB : undefined,
    }), [currencyA, currencyB]);
    // pair
    const [pairState, pair] = useUDEPair(currencies[Field.CURRENCY_A], currencies[Field.CURRENCY_B]);
    console.log(pair)
    const totalSupply = useTotalSupply(pair === null || pair === undefined ? undefined : pair.liquidityToken);
    const noLiquidity = pairState === PairState.NOT_EXISTS || Boolean(totalSupply && JSBI.equal(totalSupply.raw, ZERO));
    // balances
    const balances = useCurrencyBalances(account !== null && account !== undefined ? account : undefined, [
        currencies[Field.CURRENCY_A],
        currencies[Field.CURRENCY_B],
    ]);

    const currencyBalances = {
        [Field.CURRENCY_A]: balances[0],
        [Field.CURRENCY_B]: balances[1],
    };
    // amounts
    const independentAmount = tryParseAmount(typedValue, currencies[independentField]);
    const dependentAmount = useMemo(() => {
        if (noLiquidity) {
            if (otherTypedValue && currencies[dependentField]) {
                return tryParseAmount(otherTypedValue, currencies[dependentField]);
            }
            return undefined;
        }
        if (independentAmount) {
            // we wrap the currencies just to get the price in terms of the other token
            const wrappedIndependentAmount = wrappedCurrencyAmount(independentAmount, chainId);
            const [tokenA, tokenB] = [wrappedCurrency(currencyA, chainId), wrappedCurrency(currencyB, chainId)];
            try{
                if (tokenA && tokenB && wrappedIndependentAmount && pair) {
                    const dependentCurrency = dependentField === Field.CURRENCY_B ? currencyB : currencyA;
                    const dependentTokenAmount = dependentField === Field.CURRENCY_B
                        ? pair.priceOf(tokenA).quote(wrappedIndependentAmount)
                        : pair.priceOf(tokenB).quote(wrappedIndependentAmount);
                    return dependentCurrency === ETHER ? CurrencyAmount.ether(dependentTokenAmount.raw) : dependentTokenAmount;
                }
            }catch (e) {
                return undefined;
            }
            return undefined;
        }
        return undefined;
    }, [noLiquidity, otherTypedValue, currencies, dependentField, independentAmount, currencyA, chainId, currencyB, pair]);
    // console.log(independentAmount.token, dependentAmount)
    const parsedAmounts = useMemo(() => ({
        [Field.CURRENCY_A]: independentField === Field.CURRENCY_A ? independentAmount : dependentAmount,
        [Field.CURRENCY_B]: independentField === Field.CURRENCY_A ? dependentAmount : independentAmount,
    }), [dependentAmount, independentAmount, independentField]);
    const price = useMemo(() => {
        if (noLiquidity) {
            const { [Field.CURRENCY_A]: currencyAAmount, [Field.CURRENCY_B]: currencyBAmount } = parsedAmounts;
            if (currencyAAmount && currencyBAmount) {
                return new Price(currencyAAmount.currency, currencyBAmount.currency, currencyAAmount.raw, currencyBAmount.raw);
            }
            return undefined;
        }
        const wrappedCurrencyA = wrappedCurrency(currencyA, chainId);
        return pair && wrappedCurrencyA ? pair.priceOf(wrappedCurrencyA) : undefined;
    }, [chainId, currencyA, noLiquidity, pair, parsedAmounts]);
    // liquidity minted
    const liquidityMinted = useMemo(() => {
        const { [Field.CURRENCY_A]: currencyAAmount, [Field.CURRENCY_B]: currencyBAmount } = parsedAmounts;
        const [tokenAmountA, tokenAmountB] = [
            wrappedCurrencyAmount(currencyAAmount, chainId),
            wrappedCurrencyAmount(currencyBAmount, chainId),
        ];

        if (pair && totalSupply && tokenAmountA && tokenAmountB) {
            // console.log(tokenAmountA, tokenAmountB)
            return undefined
        }
        return undefined;
    }, [parsedAmounts, chainId, pair, totalSupply]);
    const poolTokenPercentage = useMemo(() => {
        if (liquidityMinted && totalSupply) {
            return new Percent(liquidityMinted.raw, totalSupply.add(liquidityMinted).raw);
        }
        return undefined;
    }, [liquidityMinted, totalSupply]);
    let error;
    if (!account) {
        error = 'Connect Wallet';
    }
    if (pairState === PairState.INVALID) {
        error = error !== null && error !== undefined ? error : 'Invalid pair';
    }
    if (!parsedAmounts[Field.CURRENCY_A] || !parsedAmounts[Field.CURRENCY_B]) {
        error = error !== null && error !== undefined ? error : 'Enter an amount';
    }
    const { [Field.CURRENCY_A]: currencyAAmount, [Field.CURRENCY_B]: currencyBAmount } = parsedAmounts;
    if (currencyAAmount && currencyBalances?.[Field.CURRENCY_A] && lessThan(currencyBalances?.[Field.CURRENCY_A], (currencyAAmount))) {
        error = `Insufficient ${currencies[Field.CURRENCY_A]?.symbol} balance`;
    }

    if (currencyBAmount && currencyBalances?.[Field.CURRENCY_B] && lessThan(currencyBalances?.[Field.CURRENCY_B], (currencyBAmount))) {
        error = `Insufficient ${currencies[Field.CURRENCY_B]?.symbol} balance`
    }

    return {
        dependentField,
        currencies,
        pair,
        pairState,
        currencyBalances,
        parsedAmounts,
        price,
        noLiquidity,
        liquidityMinted,
        poolTokenPercentage,
        error,
    };
}