import React, {useCallback, useEffect, useMemo, useState} from "react";

import { useDispatch } from 'react-redux'

import {currencyEquals, ETHER, JSBI, Percent, WETH} from "@udegoswap/sdk";
import { BigNumber } from '@ethersproject/bignumber'
import {useNavigate} from "react-router-dom";
import {ALL_TOKENS} from "../../contexts/TokenList";
// import TokenSelect from "./TokenSelect";
import tokens from "../../config/tokens";
import useActiveWeb3React from "../../hooks/useWeb3";
import {
    toV2LiquidityToken,
    useGasPrice,
    useIsExpertMode,
    useTrackedTokenPairs,
    useUserSlippageTolerance
} from "../../state/user/hooks";
import Select from 'react-select';
import {useCurrency} from "../../hooks/Tokens";
import {resetMintState, Field} from "../../state/mint/actions";
import {useDerivedMintInfo, useMintActionHandlers, useMintState} from "../../state/mint/hooks";
import useTransactionDeadline from "../../hooks/useTransactionDeadline";
import {maxAmountSpend} from "../../utils/maxAmountSpend";
import {ApprovalState, useApproveCallback, useApproveCallbackForRemove} from "../../hooks/useApproveCallback";
import {useTransactionAdder} from "../../state/transactions/hooks";
import {calculateGasMargin, calculateSlippageAmount, getUDERouterContract} from "../../utils";
import {unwrappedToken, wrappedCurrency} from "../../utils/wrappedCurrency";
import { UDE_ROUTER} from "../../config/config";
import {currencyId} from "../../utils/currencyId";
import {useIsTransactionUnsupported} from "../../hooks/Trades";
import {PairState} from "../../hooks/usePairs";

import Dots from "../../components/Loader/Dots";
import {ONE_BIPS} from "../../config/constants/index";
import {useTokenBalance, useTokenBalancesWithLoadingIndicator} from "../../state/wallet/hooks";
import {useUDEPair, useUDEPairs} from "../../hooks/useUDEPairs";
import {getContract, getDeadline, getMin, showError, showPromise} from "../../config/helpers";
import useTotalSupply from "../../hooks/useTotalSupply";
import TokenSelect from "./TokenSelect";
import {PoolDetails} from "./components/pool-details";
import ConnectWallet from "../../components/mtpl/ConnectWallet";


export default function MTPLCreatePool() {
    const [showModal, setShowModal] = React.useState(false)

    const { account, chainId, library } = useActiveWeb3React()
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const gasPrice = useGasPrice()

    const [currencyIdA, setCurrencyA] = useState('');
    const [currencyIdB, setCurrencyB] = useState('');

    const currencyA = useCurrency(currencyIdA)
    const currencyB = useCurrency(currencyIdB)

    useEffect(() => {
        if (!currencyIdA && !currencyIdB) {
            dispatch(resetMintState())
        }
    }, [dispatch, currencyIdA, currencyIdB])

    const oneCurrencyIsWETH = Boolean(
        chainId &&
        ((currencyA && currencyEquals(currencyA, WETH[chainId])) ||
            (currencyB && currencyEquals(currencyB, WETH[chainId]))),
    )

    const expertMode = useIsExpertMode()

    // mint state
    const { independentField, typedValue, otherTypedValue } = useMintState()
    const {
        dependentField,
        currencies,
        pair,
        pairState,
        currencyBalances,
        parsedAmounts,
        price,
        noLiquidity,
        liquidityMinted,
        poolTokenPercentage,
        error,
    } = useDerivedMintInfo(currencyA ?? undefined, currencyB ?? undefined)

    const { onFieldAInput, onFieldBInput } = useMintActionHandlers(noLiquidity)

    const isValid = !error

    // modal and loading
    const [attemptingTxn, setAttemptingTxn] = useState(false) // clicked confirm

    // txn values
    const deadline = useTransactionDeadline() // custom from users settings
    const [allowedSlippage] = useUserSlippageTolerance() // custom from users
    const [txHash, setTxHash] = useState('')

    // get formatted amounts
    const formattedAmounts = {
        [independentField]: typedValue,
        [dependentField]: noLiquidity ? otherTypedValue : parsedAmounts[dependentField]?.toSignificant(6) ?? '',
    }

    const maxAmounts = [Field.CURRENCY_A, Field.CURRENCY_B].reduce(
        (accumulator, field) => ({
                ...accumulator,
                [field]: maxAmountSpend(currencyBalances[field]),
            }),
        {},
    )

    const atMaxAmounts = [Field.CURRENCY_A, Field.CURRENCY_B].reduce(
        (accumulator, field) => ({
                ...accumulator,
                [field]: maxAmounts[field] === (parsedAmounts[field] ?? '0'),
            }),
        {},
    )

    // check whether the user has approved the router on the tokens
    const [approvalA, approveACallback] = useApproveCallback(parsedAmounts[Field.CURRENCY_A], UDE_ROUTER)
    const [approvalB, approveBCallback] = useApproveCallback(parsedAmounts[Field.CURRENCY_B], UDE_ROUTER)
    const addTransaction = useTransactionAdder()


    async function onAdd() {
        if (!chainId || !library || !account) return
        const router = getUDERouterContract(chainId, library, account)

        const { [Field.CURRENCY_A]: parsedAmountA, [Field.CURRENCY_B]: parsedAmountB } = parsedAmounts
        if (!parsedAmountA || !parsedAmountB || !currencyA || !currencyB || !deadline) {
            return
        }

        const amountsMin = {
            [Field.CURRENCY_A]: calculateSlippageAmount(parsedAmountA, noLiquidity ? 0 : allowedSlippage)[0],
            [Field.CURRENCY_B]: calculateSlippageAmount(parsedAmountB, noLiquidity ? 0 : allowedSlippage)[0],
        }

        let estimate;
        let method;
        let args;
        let value;
        if (currencyA === ETHER || currencyB === ETHER) {
            const tokenBIsETH = currencyB === ETHER
            estimate = router.estimateGas.addLiquidityETH
            method = router.addLiquidityETH
            args = [
                wrappedCurrency(tokenBIsETH ? currencyA : currencyB, chainId)?.address ?? '', // token
                (tokenBIsETH ? parsedAmountA : parsedAmountB).raw.toString(), // token desired
                amountsMin[tokenBIsETH ? Field.CURRENCY_A : Field.CURRENCY_B].toString(), // token min
                amountsMin[tokenBIsETH ? Field.CURRENCY_B : Field.CURRENCY_A].toString(), // eth min
                account,
                deadline.toHexString(),
            ]
            value = BigNumber.from((tokenBIsETH ? parsedAmountB : parsedAmountA).raw.toString())
        } else {
            estimate = router.estimateGas.addLiquidity
            method = router.addLiquidity
            args = [
                wrappedCurrency(currencyA, chainId)?.address ?? '',
                wrappedCurrency(currencyB, chainId)?.address ?? '',
                parsedAmountA.raw.toString(),
                parsedAmountB.raw.toString(),
                amountsMin[Field.CURRENCY_A].toString(),
                amountsMin[Field.CURRENCY_B].toString(),
                account,
                deadline.toHexString(),
            ]
            value = null
        }

        setAttemptingTxn(true)
        await estimate(...args, value ? { value } : {})
            .then((estimatedGasLimit) =>
                method(...args, {
                    ...(value ? { value } : {}),
                    gasLimit: calculateGasMargin(estimatedGasLimit),
                    gasPrice,
                }).then((response) => {
                    setAttemptingTxn(false)
                    showPromise(response.wait())

                    addTransaction(response, {
                        summary: `Add ${parsedAmounts[Field.CURRENCY_A]?.toSignificant(3)} ${
                            currencies[Field.CURRENCY_A]?.symbol
                        } and ${parsedAmounts[Field.CURRENCY_B]?.toSignificant(3)} ${currencies[Field.CURRENCY_B]?.symbol}`,
                    })

                    setTxHash(response.hash)
                    setShowModal(false)
                    onFieldAInput('')
                }),
            )
            .catch((err) => {
                setAttemptingTxn(false)
                showError(err.data ? err.data.message : err.message)
                // we only care if the error is something _other_ than the user rejected the tx
                if (err?.code !== 4001) {
                    console.error(err)
                }
            })
    }

    const handleCurrencyASelect = useCallback(
        (currencyA_) => {
            const newCurrencyIdA = currencyId(currencyA_)
            if (newCurrencyIdA === currencyIdB) {
                const currB = currencyIdB
                setCurrencyB(currencyIdA)
                setCurrencyA(currB)
            }else{
                setCurrencyA(newCurrencyIdA)
            }
        },
        [currencyIdB, currencyIdA],
    )
    const handleCurrencyBSelect = useCallback(
        (currencyB_) => {
            const newCurrencyIdB = currencyId(currencyB_)
            if (currencyIdA === newCurrencyIdB) {
                const currB = currencyIdB
                setCurrencyB(currencyIdA)
                setCurrencyA(currB)
            } else {
                setCurrencyB(newCurrencyIdB)
            }
        },
        [currencyIdA, currencyIdB],
    )

    const addIsUnsupported = useIsTransactionUnsupported(currencies?.CURRENCY_A, currencies?.CURRENCY_B)

    const openSwap = () => {
        navigate('/swap')
    }

    // Pool Balances
    const trackedTokenPairs = useTrackedTokenPairs()
    const tokenPairsWithLiquidityTokens = useMemo(
        () => trackedTokenPairs.map((tokens) => ({ liquidityToken: toV2LiquidityToken(tokens), tokens })),
        [trackedTokenPairs],
    )
    const liquidityTokens = useMemo(
        () => tokenPairsWithLiquidityTokens.map((tpwlt) => tpwlt.liquidityToken),
        [tokenPairsWithLiquidityTokens],
    )
    const [v2PairsBalances, fetchingV2PairBalances] = useTokenBalancesWithLoadingIndicator(
        account ?? undefined,
        liquidityTokens,
    )

    // fetch the reserves for all V2 pools in which the user has a balance
    const liquidityTokensWithBalances = useMemo(
        () =>
            tokenPairsWithLiquidityTokens.filter(({ liquidityToken }) =>
                v2PairsBalances[liquidityToken.address]?.greaterThan('0'),
            ),
        [tokenPairsWithLiquidityTokens, v2PairsBalances],
    )

    const udePair = useUDEPair(currencies[Field.CURRENCY_A], currencies[Field.CURRENCY_B])
    // console.log(udePair)

    return (
        !account ? <ConnectWallet />
            :
        <>
        <a href="javascript:;" class="px-4 py-3 text-center text-xs leading-3 text-white dark:text-black dark:hover:text-white bg-indigo-900 dark:bg-white dark:hover:bg-black rounded-lg w-auto font-medium hover:bg-blue-800 transition-all flex items-center" onClick={() => setShowModal(true)}><i class="las la-plus mr-2 text-lg leading-none"></i><span>Create Pool</span></a>
          {showModal ? (
            <>
              <div className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none px-3">
                <div className="relative w-full md:w-1/2 my-6 mx-auto">
                  {/*content*/}
                  <div className="border-0 rounded-3xl shadow-lg relative flex flex-col w-full bg-white dark:bg-black outline-none focus:outline-none">
                    {/*header*/}
                    <div className="flex items-start justify-between p-5 border-b border-solid border-blueGray-200 rounded-t">
                      <h3 className="text-lg font-semibold dark:text-white">Create Liquidity Pool</h3>
                      <button className="p-1 ml-auto bg-transparent border-0 text-black opacity-40 float-right text-3xl leading-none font-semibold outline-none focus:outline-none" onClick={() => setShowModal(false)}>
                        <span className="bg-transparent text-black dark:text-white hover:text-indigo-600 h-6 w-6 text-2xl block outline-none focus:outline-none">×</span>
                      </button>
                    </div>
                    {/*body*/}
                    <div className="relative p-6 flex-auto max-h-[550px] overflow-auto">
                      <div className="md:grid md:grid-cols-10 gap-5 md:mb-0 mb-5">
                        <div className="md:col-span-10 2xl:col-span-8">
                            <div className="mb-5 border-2 border-gray-100 border-opacity-50 dark:border-opacity-5 bg-gray-100 dark:bg-gray-200 bg-opacity-50 dark:bg-opacity-5 rounded-md shadow-sm p-3 pb-1">
                                <TokenSelect
                                    placeHolder="Token A Amount"
                                    label="Token A"
                                    isMax={!atMaxAmounts[Field.CURRENCY_A]}
                                    tokenAddress={tokens.bnb.address}
                                    tokenSymbol="bnb"
                                    inputValue={formattedAmounts[Field.CURRENCY_A]}
                                    onInputChange={onFieldAInput}
                                    onMax={() => {
                                        onFieldAInput(maxAmounts[Field.CURRENCY_A]?.toExact() ?? '')
                                    }}
                                    showApprove={false}
                                    // onApprove={approveACallback}
                                    // isDisabled={approvalA === ApprovalState.APPROVED}
                                    onItemChange={handleCurrencyASelect}
                                    // approveText={
                                    //     /* eslint-disable-next-line no-nested-ternary */
                                    //     approvalA === ApprovalState.PENDING ? (
                                    //         <Dots>{`Approving ${currencies[Field.CURRENCY_A]?.symbol}`}</Dots>
                                    //     ) : (
                                    //         approvalA === ApprovalState.APPROVED ?
                                    //             `Approved ${currencies[Field.CURRENCY_A]?.symbol}`
                                    //             :
                                    //             `Approve ${currencies[Field.CURRENCY_A]?.symbol}`
                                    //     )}
                                    selectedToken={currencies[Field.CURRENCY_A]}
                                />
                            </div>
                          
                            <div className="mb-5 border-2 border-gray-100 border-opacity-50 dark:border-opacity-5 bg-gray-100 dark:bg-gray-200 bg-opacity-50 dark:bg-opacity-5 rounded-md shadow-sm p-3 pb-1">
                                <TokenSelect
                                    placeHolder="Token B Amount"
                                    label="Token B"
                                    isMax={!atMaxAmounts[Field.CURRENCY_B]}
                                    tokenAddress={ALL_TOKENS.mlx}
                                    tokenSymbol="ude"
                                    inputValue={formattedAmounts[Field.CURRENCY_B]}
                                    onInputChange={onFieldBInput}
                                    onMax={() => {
                                        onFieldBInput(maxAmounts[Field.CURRENCY_B]?.toExact() ?? '')
                                    }}
                                    showApprove={false}
                                    // onApprove={approveBCallback}
                                    // isDisabled={approvalB === ApprovalState.APPROVED}
                                    onItemChange={handleCurrencyBSelect}
                                    selectedToken={currencies[Field.CURRENCY_B]}
                                    // approveText={
                                    //     /* eslint-disable-next-line no-nested-ternary */
                                    //     approvalB === ApprovalState.PENDING ? (
                                    //         <Dots>{`Approving ${currencies[Field.CURRENCY_B]?.symbol}`}</Dots>
                                    //     ) : (
                                    //         approvalB === ApprovalState.APPROVED ?
                                    //             `Approved ${currencies[Field.CURRENCY_B]?.symbol}` :
                                    //             `Approve ${currencies[Field.CURRENCY_B]?.symbol}`
                                    //     )}
                                />
                            </div>

                        </div>
                        <div className="md:col-span-10 2xl:col-span-2 text-center">
                            <div className="w-32 2xl:w-40 h-32 2xl:h-40 bg-gray-500 rounded-full relative mx-auto overflow-hidden">
                                <div className="w-full h-full absolute top-0 left-0 flex">
                                    <div className="w-1/2 h-full bg-blue-400 flex items-center justify-center text-white text-sm font-medium">50%</div>
                                    <div className="w-1/2 h-full bg-green-400 flex items-center justify-center text-white text-sm font-medium">50%</div>
                                </div>
                            </div>
                        </div>
                      </div>
                        <div>
                            <div gap="md">
                                {(approvalA === ApprovalState.NOT_APPROVED ||
                                    approvalA === ApprovalState.PENDING ||
                                    approvalB === ApprovalState.NOT_APPROVED ||
                                    approvalB === ApprovalState.PENDING) &&
                                isValid && (
                                    <div>
                                        {approvalA !== ApprovalState.APPROVED && (
                                            <button
                                                onClick={approveACallback}
                                                disabled={approvalA === ApprovalState.PENDING}
                                                width={approvalB !== ApprovalState.APPROVED ? '48%' : '100%'}
                                            >
                                                {approvalA === ApprovalState.PENDING ? (
                                                    <Dots>{`Enabling ${currencies[Field.CURRENCY_A]?.symbol}`}</Dots>
                                                ) : (
                                                    `Enable ${currencies[Field.CURRENCY_A]?.symbol}`
                                                )}
                                            </button>
                                        )}
                                        {approvalB !== ApprovalState.APPROVED && (
                                            <button
                                                className="px-2 py-4 mt-2 text-center text-sm text-white bg-indigo-900 rounded-lg w-full font-medium hover:bg-blue-800 transition-all"
                                                onClick={approveBCallback}
                                                disabled={approvalB === ApprovalState.PENDING}
                                                width={approvalA !== ApprovalState.APPROVED ? '48%' : '100%'}
                                            >
                                                {approvalB === ApprovalState.PENDING ? (
                                                    <Dots>{`Enabling ${currencies[Field.CURRENCY_B]?.symbol}`}</Dots>
                                                ) : (
                                                    `Enable ${currencies[Field.CURRENCY_B]?.symbol}`
                                                )}
                                            </button>
                                        )}
                                    </div>
                                )}
                            </div>
                        </div>
                      <div className="relative mb-5 flex items-center justify-between border-2 border-gray-100 border-opacity-50 dark:border-opacity-0 bg-gray-100 dark:bg-gray-200 bg-opacity-50 dark:bg-opacity-0 rounded-md shadow-sm p-3 pb-1">
                          <button
                              className="px-2 py-4 mt-2 text-center text-sm text-white bg-sky-600 rounded-lg w-full font-medium hover:bg-blue-700 transition-all"
                              onClick={onAdd}
                              disabled={!isValid || approvalA !== ApprovalState.APPROVED || approvalB !== ApprovalState.APPROVED}
                          >
                              <span>
                                  {
                                      error ?? 'Supply Liquidity'
                                  }
                              </span>
                          </button>
                      </div>
                    {
                        currencies[Field.CURRENCY_A] && currencies[Field.CURRENCY_B] && pairState !== PairState.INVALID && (
                            <>
                            <div className="grid grid-cols-1 md:grid-cols-3 sm:grid-cols-3 gap-5 md:gap-8 mb-8">
                                <div className="col-span-1 md:col-span-1">
                                    <div className="px-6 py-6 bg-white dark:bg-opacity-30 rounded-3xl shadow-2xl dark:shadow-xl hover:shadow-3xl transition-all">
                                        <div className="flex flex-wrap">
                                            <div className="w-full md:w-1/2 md:pt-6 mb-3 md:mb-0">
                                                <h3 className="mb-2 text-2xl font-bold text-white text-center md:text-left">
                                                    <span className="dark:text-white text-indigo-700">
                                                        {price?.toSignificant(6) ?? '-'}
                                                    </span>
                                                </h3>
                                                <p className="md:mb-8 text-sm font-medium text-black dark:text-indigo-100 text-center md:text-left">
                                                    {`${currencies[Field.CURRENCY_B]?.symbol ?? ''} per ${currencies[Field.CURRENCY_A]?.symbol ?? ''}`}
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-span-1 md:col-span-1">
                                    <div className="px-6 py-6 bg-white dark:bg-opacity-30 rounded-3xl shadow-2xl dark:shadow-xl hover:shadow-3xl transition-all">
                                        <div className="flex flex-wrap">
                                            <div className="w-full md:w-1/2 md:pt-6 mb-3 md:mb-0">
                                                <h3 className="mb-2 text-2xl font-bold text-white text-center md:text-left">
                                                    <span className="dark:text-white text-indigo-700">
                                                        {price?.invert()?.toSignificant(6) ?? '-'}
                                                    </span>
                                                </h3>
                                                <p className="md:mb-8 text-sm font-medium text-black dark:text-indigo-100 text-center md:text-left">
                                                    {`${currencies[Field.CURRENCY_A]?.symbol ?? ''} per ${currencies[Field.CURRENCY_B]?.symbol ?? ''}`}
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-span-1 md:col-span-1">
                                    <div className="px-6 py-6 bg-white dark:bg-opacity-30 rounded-3xl shadow-2xl dark:shadow-xl hover:shadow-3xl transition-all">
                                        <div className="flex flex-wrap">
                                            <div className="w-full md:w-1/2 md:pt-6 mb-3 md:mb-0">
                                                <h3 className="mb-2 text-2xl font-bold text-white text-center md:text-left">
                                                    <span className="dark:text-white text-indigo-700">
                                                        {noLiquidity && price
                                                            ? '100'
                                                            : (poolTokenPercentage?.lessThan(ONE_BIPS) ? '<0.01' : poolTokenPercentage?.toFixed(2)) ?? '0'}
                                                        %
                                                    </span>
                                                </h3>
                                                <p className="md:mb-8 text-sm font-medium text-black dark:text-indigo-100 text-center md:text-left">
                                                    Share of Pool
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </>
                        )
                    }
                    {
                        udePair[1] &&
                        <PoolDetails pair={udePair[1]} />
                    }
                    </div>
                  </div>
                </div>
              </div>
              <div className="opacity-75 fixed inset-0 z-40 bg-black backdrop-blur-xl"></div>
            </>
          ) : null}
        </>
    )
}