import React from 'react';
import PropTypes from 'prop-types';
import {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "../../redux/store";
import {getPoolTokens, getTokens} from "../../redux/slices/tokens";
import {escapeRegExp} from "../../utils";
import {useCurrencyBalance} from "../../state/wallet/hooks";
import useActiveWeb3React from "../../hooks/useWeb3";
import ListToken from './ListToken';
import getTokenLogoURL from "../../utils/getTokenLogoURL";
import CurrencyLogo from "../../components/Logo/CurrencyLogo";


const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`)


export default function TokenSelect(
    {
        tokenSymbol = "bnb",
        isMax=false,
        tokenAddress="",
        label="",
        placeHolder="",
        inputValue="",
        onInputChange=null,
        onItemChange=null,
        tokenImg='./tokens/bnb.png',
        showApprove=true,
        onApprove=null,
        approveText='',
        onMax=null,
        selectedToken,
        isDisabled=true
    }
) {

    const [symbol, setSymbol] = useState(tokenSymbol);
    const [token, setToken] = useState(tokenImg);
    const [address, setAddress] = useState(tokenAddress);
    const { account } = useActiveWeb3React()

    const [isDialogOpen, setDialog] = useState(false);

    const dispatch = useDispatch();
    const { tokens, pooltokens } = useSelector((state) => state.tokens);

    useEffect(() => {
        dispatch(getPoolTokens());
    }, [dispatch]);

    const selectDialog = () => {
        setDialog(true)
    }

    const handleClose = () => {
        setDialog(false)
    }

    const tokenSelected = (item) => {
        // console.log(item)
        setToken(item.tokenImg)
        setAddress(item.address)
        setSymbol(item.symbol)
        setDialog(false)
        onItemChange(item)
    }

    const enforcer = (nextUserInput) => {
        if (nextUserInput === '' || inputRegex.test(escapeRegExp(nextUserInput))) {
            onInputChange(nextUserInput)
        }
    }
    const selectedCurrencyBalance = useCurrencyBalance(account ?? undefined, selectedToken ?? undefined)

    return (
        <>
            <div className="token-select-input">
                <div className="flex flex-row items-center justify-between">
                    <div className="text-sm text-black dark:text-white text-opacity-50 hover:text-opacity-100 font-semibold">{label}</div>
                    <div className="text-sm text-black dark:text-gray-400 hover:text-indigo-500 dark:hover:text-white">Balance: {selectedCurrencyBalance?.toSignificant(6) ?? 'Loading' }</div>
                </div>
                <div className="token-row flex flex-row items-center justify-between my-2">
                <button className="select-source-token token-row flex flex-row items-center justify-start" onClick={selectDialog}>
                    <div className="tokan-icon mr-2 w-6">
                        {/*<img src={token} alt="" />*/}
                        <CurrencyLogo currency={selectedToken} />
                    </div>
                    <div className="token-name text-2sm text-black dark:text-white font-semibold mt-1">
                        {(selectedToken && selectedToken.symbol && selectedToken.symbol.length > 20
                        ? `${selectedToken.symbol.slice(0, 4)}...${selectedToken.symbol.slice(
                            selectedToken.symbol.length - 5,
                            selectedToken.symbol.length,
                            )}`
                        : selectedToken?.symbol) || 'Select'}
                    </div>
                    <i className="las la-angle-down ml-2 text-black dark:text-white" />
                </button>
                <div className="tokan-input">
                    <input
                        className="bg-transparent border-0 p-0 bg-gray-100 w-full outline-none text-sm appearance-none"
                        inputMode="decimal"
                        maxLength={18}
                        pattern="^[0-9]*[.,]?[0-9]*$"
                        x-inputmode="decimal"
                        tabIndex={0}
                        defaultValue={1}
                        type="text"
                        fullWidth
                        value={inputValue}
                        variant="filled"
                        label={label}
                        placeholder={"0.0"}
                        title={label}
                        onInput={(event) => {
                            enforcer(event.target.value.replace(/,/g, '.'))
                        }}
                    />
                </div>
            </div>
            </div>
            {isDialogOpen ? (
              <>
                <div className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none px-3">
                  <div className="relative w-auto my-6 mx-auto max-w-xl">
                    {/*content*/}
                    <div className="border-0 rounded-3xl shadow-lg relative flex flex-col w-full bg-white dark:bg-indigo-900 outline-none focus:outline-none">
                      {/*header*/}
                      <div className="flex items-start justify-between p-5 border-b border-solid border-blueGray-200 dark:border-gray-100 dark:border-opacity-10 rounded-t">
                        <h3 className="text-lg font-semibold">Select a Token</h3>
                        <button className="p-1 ml-auto bg-transparent border-0 text-black opacity-40 float-right text-3xl leading-none font-semibold outline-none focus:outline-none" onClick={handleClose}>
                          <span className="bg-transparent text-black dark:text-white hover:text-indigo-600 h-6 w-6 text-2xl block outline-none focus:outline-none">×</span>
                        </button>
                      </div>
                      {/*body*/}
                      <div className="relative p-6 flex-auto">
                        {/* <form action role="search" noValidate className="DocSearch-Form px-5 py-4 border border-gray-200 dark:border-gray-200 hover:border-indigo-600 dark:border-gray-500 dark:hover:border-white mb-2 rounded-xl">
                          <label className="DocSearch-MagnifierLabel" htmlFor="docsearch-input" id="docsearch-label">
                            <svg width={20} height={20} className="DocSearch-Search-Icon" viewBox="0 0 20 20"><path d="M14.386 14.386l4.0877 4.0877-4.0877-4.0877c-2.9418 2.9419-7.7115 2.9419-10.6533 0-2.9419-2.9418-2.9419-7.7115 0-10.6533 2.9418-2.9419 7.7115-2.9419 10.6533 0 2.9419 2.9418 2.9419 7.7115 0 10.6533z" stroke="stroke-black dark:stroke-white" fill="none" fillRule="evenodd" strokeLinecap="round" strokeLinejoin="round" /></svg>
                          </label>
                          <input className="DocSearch-Input font-semibold" aria-autocomplete="list" aria-labelledby="docsearch-label" id="docsearch-input" autoComplete="off" autoCorrect="off" autoCapitalize="off" spellCheck="false" placeholder="Search Tokens" maxLength={64} type="search" defaultValue />
                          <button type="reset" title="Clear the query" className="DocSearch-Reset" hidden><svg width={20} height={20} viewBox="0 0 20 20"><path d="M10 10l5.09-5.09L10 10l5.09 5.09L10 10zm0 0L4.91 4.91 10 10l-5.09 5.09L10 10z" stroke="#ffffff" fill="none" fillRule="evenodd" strokeLinecap="round" strokeLinejoin="round" /></svg></button>
                        </form> */}
                        {/* <div className="favourite-tokens-container flex flex-wrap align-middle justify-center p-3 px-0 border-b-2 border-gray-100 border-opacity-25">
                          <a href="javascrip:;" className="favourite-token px-2 py-2 flex align-middle justify-center bg-white bg-opacity-25 border border-gray-500 dark:border-gray-100 m-1 relative rounded-md hover:border-gray-700 dark:hover:border-white"><div className="rounded-full overflow-hidden w-6 mx-1"><img src="images/dollar.svg" className="w-full rounded-3xl" alt="" /></div><span className="text-sm text-black dark:text-white mr-2 mt-1">BNB</span><span className="favorite-close text-gray-600 hover:text-red-800"><i className="las la-times" /></span></a>
                          <a href="javascrip:;" className="favourite-token px-2 py-2 flex align-middle justify-center bg-white bg-opacity-25 border border-gray-500 dark:border-gray-100 m-1 relative rounded-md hover:border-gray-700 dark:hover:border-white"><div className="rounded-full overflow-hidden w-6 mx-1"><img src="images/dollar.svg" className="w-full rounded-3xl" alt="" /></div><span className="text-sm text-black dark:text-white mr-2 mt-1">BNB</span><span className="favorite-close text-gray-600 hover:text-red-800"><i className="las la-times" /></span></a>
                          <a href="javascrip:;" className="favourite-token px-2 py-2 flex align-middle justify-center bg-white bg-opacity-25 border border-gray-500 dark:border-gray-100 m-1 relative rounded-md hover:border-gray-700 dark:hover:border-white"><div className="rounded-full overflow-hidden w-6 mx-1"><img src="images/dollar.svg" className="w-full rounded-3xl" alt="" /></div><span className="text-sm text-black dark:text-white mr-2 mt-1">BNB</span><span className="favorite-close text-gray-600 hover:text-red-800"><i className="las la-times" /></span></a>
                          <a href="javascrip:;" className="favourite-token px-2 py-2 flex align-middle justify-center bg-white bg-opacity-25 border border-gray-500 dark:border-gray-100 m-1 relative rounded-md hover:border-gray-700 dark:hover:border-white"><div className="rounded-full overflow-hidden w-6 mx-1"><img src="images/dollar.svg" className="w-full rounded-3xl" alt="" /></div><span className="text-sm text-black dark:text-white mr-2 mt-1">BNB</span><span className="favorite-close text-gray-600 hover:text-red-800"><i className="las la-times" /></span></a>
                        </div> */}
                        <div className="rounded-b-3xl overflow-hidden">
                          <div className="token-list">
                            {
                                !pooltokens && <div loading>Loading Token List...</div>
                            }
                            {pooltokens?.map((tokenItem) => <ListToken key={tokenItem?.address} item={tokenItem} tokenSelected={tokenSelected} selectedToken={selectedToken} currency={tokenItem} />)}
                          </div>
                        </div>
                      </div>
                      {/*footer*/}
                      <div className="flex items-center flex-col justify-end px-6 py-4 border-t border-solid border-blueGray-200 dark:border-gray-100 dark:border-opacity-10 rounded-b">
                        <button
                          className="bg-indigo-500 hover:bg-sky-600 text-white active:bg-sky-600 font-bold uppercase text-xs w-full px-6 py-3 rounded-xl shadow hover:shadow-lg outline-none focus:outline-none ease-linear transition-all duration-150" type="button" onClick={handleClose}>Close</button>
                        {/* <button className="bg-emerald-500 text-white active:bg-emerald-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150" type="button" onClick={() => setShowModal(false)}>Save Changes</button> */}
                      </div>
                    </div>
                  </div>
                </div>
                <div className="opacity-75 fixed inset-0 z-40 bg-black backdrop-blur-xl"></div>
              </>
            ) : null}
        </>
    )
}
